import { instance } from './url'

const getmessages = async () => {
  return await instance
    .get('/admin/nowmessages', { credentials: true })
    .catch((err) => {
      return err.response
    })
}

const cutmessage = async (data) => {
  return await instance
    .post('/admin/cut', data, { credentials: true })
    .catch((err) => {
      return err.response
    })
}

const awardmessage = async (data) => {
  return await instance
    .post('/select/awards', data, { credentials: true })
    .catch((err) => {
      return err.response
    })
}

const getindex = async () => {
  return await instance.get('/index', { credentials: true }).catch((err) => {
    return err.response
  })
}

const geteventend = async () => {
  return await instance
    .get('/apply/event/terminate', { credentials: true })
    .catch((err) => {
      return err.response
    })
}

export { getmessages, cutmessage, awardmessage, getindex, geteventend }
