<script>
import * as api from "../../api/api-list";

export default {
  data() {
    return {
      messages: [],
      temp_messages: [],
      awardflaglist: [],
      awardtemp: [],
      blockflaglist: [],
      reverseblockflaglist: [],
      awardlist: [],
      cutlist: [],
      eventindex: 1,
      selectitem: [
        { text: "1차 이벤트", value: 1 },
        { text: "2차 이벤트", value: 2 },
        { text: "3차 이벤트", value: 3 },
        { text: "4차 이벤트", value: 4 },
        { text: "5차 이벤트", value: 5 },
      ],
      modalflag: false,
      modaltypeflag: 0,
      modaltext: "",
      awardflag: true,
    };
  },
  async created() {
    const res = await api.getmessages();
    this.messages = res.data.result;
    this.temp_messages = res.data.result;
    this.temp_messages = this.temp_messages.sort(function (a, b) {
      if (a.date > b.date) return 1;
      else if (a.date < b.date) return -1;
      else return 0;
    });
    this.changeeventindex();
    this.setawardflag(this.messages);
    this.setblockflag(this.messages);
    const res2 = await api.getindex();
    if (res2.data > this.eventindex) {
      this.awardflag = false;
    }
  },
  methods: {
    offmodal() {
      this.modaltext = "";
      this.modalflag = false;
      this.modaltypeflag = -1;
    },
    setawardflag(arr) {
      for (let i = 0; i < arr.length; i++) {
        this.awardflaglist.push(arr[i].isSelected);
      }
    },
    setblockflag(arr) {
      for (let i = 0; i < arr.length; i++) {
        this.blockflaglist.push(arr[i].block_status);
      }
    },
    awardmsg() {
      let amount = 0;
      for (let i = 0; i < this.temp_messages.length; i++) {
        if (this.temp_messages[i].isSelected) {
          amount += 1;
        }
        if (amount > 2) {
          console.log(amount);
          return;
        }
      }

      if (this.awardlist.length == 0) {
        console.log("empty");
        return;
      }

      api.awardmessage({ arr: this.awardlist });
      this.awardlist = [];
    },
    blockmsg() {
      if (this.cutlist.length == 0) {
        return;
      }
      api.cutmessage({ arr: this.cutlist });
      this.cutlist = [];
    },
    selectaward(data, index) {
      if (this.awardflag == false) {
        return;
      }
      this.temp_messages[index].isSelected =
        !this.temp_messages[index].isSelected;
      if (this.awardlist.includes(data)) {
        for (let i = 0; i < this.awardlist.length; i++) {
          if (this.awardlist[i] === data) {
            this.awardlist.splice(i, 1);
            i--;
          }
        }
      } else {
        if (this.awardlist.length == 5) {
          this.modaltext = "당첨자는 5명 이상 지정할 수 없습니다";
          this.modaltypeflag = 0;
          this.modalflag = true;
          this.temp_messages[index].isSelected = false;
          return;
        }
        this.awardlist.push(data);
      }
      console.log(this.awardlist);
    },
    selectblock(data, index) {
      this.temp_messages[index].block_status =
        !this.temp_messages[index].block_status;
      if (this.cutlist.includes(data)) {
        for (let i = 0; i < this.cutlist.length; i++) {
          if (this.cutlist[i] === data) {
            this.cutlist.splice(i, 1);
            i--;
          }
        }
      } else {
        this.cutlist.push(data);
        console.log(this.cutlist);
      }
    },
    async changeeventindex() {
      const flag = await api.geteventend();
      console.log(flag);
      if (flag.data.result == false) {
        this.awardflag = false;
      }
      //let arr = [];

      // if (this.eventindex == "0") {
      //   this.temp_messages = this.messages;
      //   return;
      // }

      // for (let i = 0; i < this.messages.length; i++) {
      //   if (this.messages[i].index.toString() == this.eventindex.toString()) {
      //     arr.push(this.messages[i]);
      //   } else {
      //     continue;
      //   }
      // }
      this.temp_messages = this.messages;
    },
    awardmodal() {
      if (this.awardlist.length == 0) {
        this.modaltypeflag = 0;
        this.modaltext = "당첨자를 선정해주세요.";
        this.modalflag = true;
        return;
      }
      this.modaltypeflag = 1;
      this.modaltext = "당첨 확정하시겠습니까?";
      this.modalflag = true;
    },
    blockmodal() {
      if (this.cutlist.length == 0) {
        this.modaltypeflag = 0;
        this.modaltext = "차단할 메시지를 선택해주세요.";
        this.modalflag = true;
        return;
      }
      this.modaltypeflag = 2;
      this.modaltext = "차단 확정하시겠습니까?";
      this.modalflag = true;
    },
    goselect() {
      const data = { arr: this.awardlist };
      api.awardmessage(data);
      this.awardflag = false;
      this.offmodal();
    },
    goblock() {
      if (this.cutlist.length == 0) {
        return;
      }
      api.cutmessage({ arr: this.cutlist });
      this.cutlist = [];
      this.offmodal();
    },
    today() {
      const date = new Date();

      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();

      return `${year}년 ${month}월 ${day}일 이벤트`;
    },
  },
};
</script>

